// app/catalog/vehicles/page.tsx
"use client"
import * as React from "react"
import { Input } from "@/components/ui/input"

export default function Page() {
  const [manus,setManus]=React.useState<any[]>([])
  const [models,setModels]=React.useState<any[]>([])
  const [types,setTypes]=React.useState<any[]>([])
  const [mid,setMid]=React.useState<number|undefined>()
  const [msid,setMsid]=React.useState<number|undefined>()

  React.useEffect(()=>{(async()=>{
    const r=await fetch("/api/vehicles?mode=manufacturers"); setManus(await r.json())
  })()},[])

  async function pickManu(id:number){
    setMid(id); setModels([]); setTypes([])
    const r=await fetch(`/api/vehicles?mode=models&manufacturerId=${id}`); setModels(await r.json())
  }
  async function pickModel(id:number){
    setMsid(id); setTypes([])
    const r=await fetch(`/api/vehicles?mode=types&modelSeriesId=${id}`); setTypes(await r.json())
  }

  return (
    <div className="p-6 grid gap-6 md:grid-cols-3">
      <div>
        <h3 className="font-semibold mb-2">Merken</h3>
        <div className="space-y-1 max-h-80 overflow-auto">
          {manus?.length ? manus.map((m:any)=>(
            <button key={m.id} onClick={()=>pickManu(m.id)} className="w-full text-left px-3 py-1 rounded hover:bg-muted">
              {m.name}
            </button>
          )) : <p className="text-sm text-muted-foreground">Geen data</p>}
        </div>
      </div>
      <div>
        <h3 className="font-semibold mb-2">Modellen</h3>
        <div className="space-y-1 max-h-80 overflow-auto">
          {models?.length ? models.map((m:any)=>(
            <button key={m.id} onClick={()=>pickModel(m.id)} className="w-full text-left px-3 py-1 rounded hover:bg-muted">
              {m.name}
            </button>
          )) : <p className="text-sm text-muted-foreground">Selecteer eerst een merk</p>}
        </div>
      </div>
      <div>
        <h3 className="font-semibold mb-2">Types / Bouwjaar</h3>
        <div className="space-y-1 max-h-80 overflow-auto">
          {types?.length ? types.map((t:any)=>(
            <div key={t.id} className="px-3 py-1 rounded bg-muted/30">{t.name}</div>
          )) : <p className="text-sm text-muted-foreground">Selecteer eerst een model</p>}
        </div>
      </div>
    </div>
  )
}
